import React from "react";
import Footer from "./scenes/components/Footer";
import Footsteps from "./scenes/components/FootSteps/FootStepsGlobal";
// Business
import BusinessHome from "./scenes/Business/Home/Home";
import BusinessSolution from "./scenes/Business/Solution/Solution";
import BusinessProcess from "./scenes/Business/Process/Process";
import BusinessAdvantajes from "./scenes/Business/Advantajes/Advantajes";
// Client
import ClientHome from "./scenes/Client/Home/Home";
import ClientSolution from "./scenes/Client/Solution/Solution";
import ClientProcess from "./scenes/Client/Process/Process";
import ClientAdvantajes from "./scenes/Client/Advantajes/Advantajes";
// Presentation
import Presentation from "./scenes/components/Presentation/Presentation";
// Contact
import Contact from "./scenes/components/Contact/Contact";
// NavBar
import NavBar from "./scenes/components/NavBar/NavBar";
// router
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
// styles
import { styled } from "@material-ui/core/styles";
import { ThemeProvider } from "@material-ui/core/styles";
import theme from "./theme";
import business_theme from "./business_theme";

const rootId = "app-root";
const Root = styled("div")({
  width: "100%",
  height: "100%",
  display: "flex",
  flexDirection: "column",
  overflow: "hidden"
});

const App = () => {
  return (
    <Router basename="/">
      <Switch>
        <Root id={rootId}>
          <Route exact path="/home">
            <Footsteps />
            <ThemeProvider theme={theme}>
              <NavBar />
              <ClientHome />
              <Presentation />
              <ClientSolution />
              <ClientProcess />
              <ClientAdvantajes />
              <Contact />
              <Footer />
            </ThemeProvider>
          </Route>
          <Route exact path="/business">
            <ThemeProvider theme={business_theme}>
              <NavBar />
              <BusinessHome />
              <Presentation />
              <BusinessSolution />
              <BusinessProcess />
              <BusinessAdvantajes />
              <Contact />
              <Footer />
            </ThemeProvider>
          </Route>
        </Root>
      </Switch>
    </Router>
  );
};

export default App;
