import React from "react";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
// services
import { scrollToDiv, scrollToTop } from "../../services";
// styles
import { styled } from "@material-ui/core/styles";

const AppBar = styled("div")(({ theme }) => ({
  width: "100%",
  height: theme.heights.navBarMobile,
  position: "fixed",
  zIndex: 100,
  backgroundColor: theme.palette.white.main,
  display: "flex",
  flexDirection: "column",
  justifyContent: "center",
  alignItems: "center",
  boxShadow: theme.shadows.navBarShadow
}));

const TitleMenuDiv = styled("div")({
  display: "flex"
});

const TitleMenuSwitchProfileDiv = styled("div")({
  width: "100%",
  display: "flex",
  justifyContent: "space-between",
  alignItems: "center"
});

const Title = styled("h1")(({ theme }) => ({
  margin: 0,
  marginLeft: 10,
  padding: 0,
  display: "flex",
  alignItems: "center",
  fontSize: 20,
  color: theme.palette.primary.main
}));

const Menu = styled("div")({
  width: "100%",
  display: "flex",
  flexDirection: "column",
  justifyContent: "flex-start",
  marginLeft: 20
});

const Collapse = styled("div")(({ theme, extend }) => ({
  position: "absolute",
  top: theme.heights.navBarMobile,
  width: "100%",
  height: extend ? "20vh" : 0,
  transition: "height 300ms",
  backgroundColor: theme.palette.white.main,
  overflow: "hidden",
  boxShadow: theme.shadows.navBarShadow
}));

const SectionTitle = styled("h1")(({ theme }) => ({
  height: "2vh",
  margin: "1vh 0px",
  marginLeft: 10,
  padding: 0,
  fontSize: 15,
  color: theme.palette.black.main
}));

const NavBarMobile = () => {
  const [showSections, setShowSections] = React.useState(false);
  /* make nav bar visible */
  React.useEffect(() => {
    const scrollTracker = () => {
      setShowSections(false);
    };

    document.addEventListener("scroll", scrollTracker);
    return () => {
      document.removeEventListener("scroll", scrollTracker);
    };
  }, []);

  /* on first render, scroll to top */
  React.useEffect(() => {
    scrollToTop();
  }, []);

  return (
    <AppBar extend={showSections}>
      <TitleMenuSwitchProfileDiv>
        <TitleMenuDiv>
          <IconButton onClick={() => setShowSections(_ => !_)}>
            <MenuIcon />
          </IconButton>
          <Title onClick={scrollToTop}>PlanInsta</Title>
        </TitleMenuDiv>
      </TitleMenuSwitchProfileDiv>
      <Collapse extend={showSections}>
        <Menu>
          <SectionTitle onClick={() => scrollToDiv("presentation", true)}>
            PREZENTARE
          </SectionTitle>
          <SectionTitle onClick={() => scrollToDiv("solution", true)}>
            SOLUȚIE
          </SectionTitle>
          <SectionTitle onClick={() => scrollToDiv("process", true)}>
            PROCES
          </SectionTitle>
          <SectionTitle onClick={() => scrollToDiv("advantajes", true)}>
            AVANTAJE
          </SectionTitle>
          <SectionTitle onClick={() => scrollToDiv("contact", true)}>
            CONTACT
          </SectionTitle>
        </Menu>
      </Collapse>
    </AppBar>
  );
};

export default NavBarMobile;
