import React from "react";
import FootStepsLine from "./FootStepsLine";
import { LineLength } from "./FootStepsLine";
import { useTheme } from "@material-ui/core/styles";
import useMediaQuery from "@material-ui/core/useMediaQuery";

const desktop = {
  baseLine: 0,
  leftOffset: 60,
  rightOffset: 60,

  rightLinesQuantity: 6,
  leftLineQuantity: 5
};

const mobile = {
  baseLine: 200,
  leftOffset: 15,
  rightOffset: 15,

  rightLinesQuantity: 6,
  leftLineQuantity: 5
};

const FootStepsGlobal = () => {
  const theme = useTheme();
  const matchesDesktop = useMediaQuery(theme.breakpoints.up("lg"));

  let device = desktop;

  if (!matchesDesktop) device = mobile;

  return (
    <>
      {/* left part */}
      {new Array(device.leftLineQuantity).fill().map((_, index) => {
        if (index++ % 2)
          return (
            <FootStepsLine
              top={device.baseLine + index * LineLength}
              left={device.leftOffset}
            />
          );
        return null;
      })}
      {/* right part  */}
      {new Array(device.rightLinesQuantity).fill().map((_, index) => {
        if (index % 2)
          return (
            <FootStepsLine
              top={device.baseLine + index * LineLength}
              right={device.rightOffset}
            />
          );
        return null;
      })}
    </>
  );
};

export default FootStepsGlobal;
