import React from "react";
import { styled } from "@material-ui/core/styles";
import FootSteps from "./FootSteps.jsx";

export const LineLength = 600;

const Line = styled("div")(({ top, left, right }) => ({
  height: LineLength,
  display: "flex",
  flexDirection: "column",
  justifyContent: "space-around",
  position: "absolute",
  top,
  left,
  right
}));

const FootStepsLine = ({ top, left, right }) => {
  return (
    <Line {...{ top, left, right }}>
      <FootSteps deg={15} />
      <FootSteps />
      <FootSteps deg={-15} />
      <FootSteps />
      <FootSteps deg={15} />
      <FootSteps />
      <FootSteps deg={-15} />
    </Line>
  );
};

export default FootStepsLine;
