import React from "react";
import ReadMore from "../../components/Misc/SubmitButton";
import Dividier from "../../components/Misc/Divider";
import { useTheme } from "@material-ui/core/styles";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import LandingTitle from "../../components/Home/LandingTitle";
import LandingDescription from "../../components/Home/LandingDescription";
import ContentImg from "../../components/Home/ContentImg";
import PresentationDiv from "../../components/Home/PresentationDiv";
// services
import { scrollToDiv } from "../../services";
// statiscs
import Background from "./assets/online-store.svg";
// styles
import { styled } from "@material-ui/core/styles";

const Wrapper = styled("div")(({ theme }) => ({
  width: "100vw",
  height: "110vh",
  color: theme.palette.white.main,
  backgroundColor: theme.palette.primary.main,
  boxShadow: theme.shadows.sectionShadow
}));

const Home = () => {
  const theme = useTheme();
  const matchesDesktop = useMediaQuery(theme.breakpoints.up("lg"));

  return (
    <Wrapper>
      <PresentationDiv>
        <LandingTitle>PlanInsta Business</LandingTitle>
        <Dividier />
        <LandingDescription>
          Înfrumusețare și relaxare la un click distanță!
        </LandingDescription>
        <ReadMore
          size="large"
          onClick={() => scrollToDiv("presentation", !matchesDesktop)}
        />
      </PresentationDiv>
      <ContentImg id="home" url={Background} />
    </Wrapper>
  );
};

export default Home;
