import React from "react";
// styles
import { styled } from "@material-ui/core/styles";
// statics
import FootstepsImg from "./assets/footsteps.png";

const ImgWrapper = styled("img")(({ deg = 0 }) => ({
  height: 30,
  width: "auto",
  transform: `rotate(${deg + 180}deg)`,
  opacity: 0.5
}));

const Footsteps = ({ deg }) => {
  return <ImgWrapper src={FootstepsImg} alt={"Footsteps"} deg={deg} />;
};

export default Footsteps;
