import React from "react";
// services
import { scrollToDiv, scrollToTop } from "../../services";
// styles
import { styled } from "@material-ui/core/styles";
import { useTheme } from "@material-ui/core/styles";

const NavBar = styled("header")(({ theme, hide }) => ({
  width: "100vw",
  height: theme.heights.navBar,
  position: "fixed",
  display: "flex",
  justifyContent: "center",
  alignItems: "center",
  zIndex: 100,
  color: hide ? theme.palette.white.main : theme.palette.primary.main,
  transition: "color 300ms linear",
  background: !hide && theme.palette.white.main,
  // eslint-disable-next-line no-dupe-keys
  transition: "background-color 300ms linear",
  fontSize: 15,
  boxShadow: theme.shadows.navBarShadow
}));

const Title = styled("h1")({
  height: "100%",
  margin: 0,
  display: "flex",
  alignItems: "center",
  padding: 0,
  cursor: "pointer",
  fontSize: "2em",
  transition: "transform 300ms",
  "&:hover": {
    transform: "scale(1.1)"
  }
});

const LogoDiv = styled("div")({
  width: "40%",
  height: "100%",
  display: "flex",
  justifyContent: "center"
});

const Menu = styled("div")({
  width: "100%",
  height: "100%",
  display: "flex",
  justifyContent: "space-between",
  alignItems: "center"
});

const MenuDiv = styled("div")({
  width: "35%",
  height: "100%",
  marginLeft: "20%",
  marginRight: "15%",
  display: "flex",
  justifyContent: "center"
});

const SelectionTitle = styled("a")({
  fontWeight: 550,
  fontSize: "1em",
  cursor: "pointer",
  transition: "transform 300ms",
  "&:hover": {
    transform: "scale(1.1)"
  }
});

const NavBarDesktop = ({ location }) => {
  const theme = useTheme();
  const [hide, setHide] = React.useState(true);

  /* make nav bar visible */
  React.useEffect(() => {
    const scrollTracker = () => {
      const elem = document.getElementById("app-root");
      const rect = elem.getBoundingClientRect();

      if (-rect.top > theme.heights.navBar) {
        setHide(false);
      } else {
        setHide(true);
      }
    };

    document.addEventListener("scroll", scrollTracker);
    return () => {
      document.removeEventListener("scroll", scrollTracker);
    };
  }, [theme]);

  /* on first render, scroll to top */
  React.useEffect(() => {
    scrollToTop();
  }, []);

  return (
    <NavBar {...{ hide }}>
      <LogoDiv>
        <Title onClick={scrollToTop} {...{ hide }}>
          PlanInsta
        </Title>
      </LogoDiv>
      <MenuDiv>
        <Menu>
          <SelectionTitle
            onClick={() => scrollToDiv("presentation")}
            {...{ hide }}
          >
            PREZENTARE
          </SelectionTitle>
          <SelectionTitle onClick={() => scrollToDiv("solution")} {...{ hide }}>
            SOLUȚIE
          </SelectionTitle>
          <SelectionTitle onClick={() => scrollToDiv("process")} {...{ hide }}>
            PROCES
          </SelectionTitle>
          <SelectionTitle
            onClick={() => scrollToDiv("advantajes")}
            {...{ hide }}
          >
            AVANTAJE
          </SelectionTitle>
          <SelectionTitle onClick={() => scrollToDiv("contact")} {...{ hide }}>
            CONTACT
          </SelectionTitle>
        </Menu>
      </MenuDiv>
    </NavBar>
  );
};

export default NavBarDesktop;
